    .main-header .logo svg {
        display: none
    }
    .main-header .logo .print-logo {
        display: block
    }
    .left-holder,
    .main-footer {
        display: none
    }
    .track-holder {
        border: 1px solid #e4e4e4;
        margin: 0 auto
    }
    .track-holder .details-box ul {
        width: 50%;
        float: left;
        border-top: none;
        padding-top: 0;
        margin-top: 0
    }
    .track-holder .rest-info {
        float: right;
        width: 50%
    }
    .track-holder .rest-info figure {
        width: 50px;
        height: 50px
    }
    .track-holder .rest-info img {
        width: 100%
    }
    .track-holder .rest-info h3 {
        line-height: 24px
    }
    .factor-holder footer {
        float: left;
        width: 300px
    }
}



